.myDivider{
  display: table;
  white-space: nowrap;
  height: auto;
  overflow: hidden;
  line-height: 2;
  text-align: center;
  color: #666;
  text-align: center;
  width: 40%;
  padding-left: 30%;
  padding-right: 30%;
}
.myDivider:after, .myDivider:before {
  content: "";
  display: table-cell;
  position: relative;
  top: 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}
.myDivider:before {
  background-position: right 1em top 50%;
  width: 50%;
}
.myDivider:after {
  background-position: left 1em top 50%;
  width: 50%;
}

// Picker Modal
.myAdLayer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 60%;
  z-index: 12500;
  display: none;
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -ms-transition-property: -ms-transform;
  -o-transition-property: -o-transform;
  transition-property: transform;
  background: #cfd5da;
  .translate3d(0,-100%,0);
  &.modal-in, &.modal-out {
    .transition(400ms);
    &.not-animated {
      .transition(0ms);
    }
  }
  &.modal-in {
    .translate3d(0,0,0);
  }
  &.modal-out {
    .translate3d(0,-100%,0);
  }
  .picker-modal-inner {
    height: 100%;
    position: relative;
  }
  .toolbar {
    .hairline(top, #929499);
    position: relative;
    width: 100%;
    background: #f7f7f8;
    + .picker-modal-inner {
      height: ~"-webkit-calc(100% - @{toolbarSize})";
      height: ~"-moz-calc(100% - @{toolbarSize})";
      height: ~"calc(100% - @{toolbarSize})";
    }
  }
  &.picker-modal-inline, .popover & {
    display: block;
    position: relative;
    background: none;
    z-index: inherit;
    .translate3d(0,100%,0);
    .toolbar {
      .hairline-remove(top);
      .hairline(bottom, #929499);
    }
  }
  .popover & {
    width: auto;
    .toolbar {
      background: none;
    }
  }
  &.smart-select-picker {
    .page {
      background: #fff;
    }
    .toolbar {
      .hairline(bottom, #c4c4c4);
    }
    .list-block {
      margin: 0;
      ul {
        .hairline-remove(top);
        .hairline-remove(bottom);
      }
    }
  }
}



